n                      <td> {{ msg.message }}</td>\n                      <td> {{ msg.created_at }}</td>\n                      <td>\n                        <button class=\"btn btn-xs btn-danger\" @click=\"deleteMessge(msg)\">Delete</button>\n                      </td>\n                    </tr>\n                  </tbody>\n                </table>\n              </div>\n            </div>\n          </div>\n        </div>\n      </div>\n    </div>\n\n</template>\n\n<script>\nexport default {\n  created() {\n    swal(\"Retrieving all messages....\", {\n      buttons: false,\n    });\n    this.loading=true;\n      axios.get('api/get-all-messages').then( rsp => {\n        this.messages = rsp.data;\n        this.loading=false;\n        swal.stopLoading();\n        swal.close();\n      }, err => {\n        this.serverError(err);\n      });\n  },\n  data () {\n    return {\n      searchText: '',\n      error: '',\n      messages: {},\n      loading: false\n    };\n  },\n  computed: {},\n  methods: {\n   serverError(msg){\n     swal(\"Error!\", `${msg}`, \"error\");\n   },\n   deleteMessge(message){\n     swal({\n       title: \"Are you sure?\",\n       text: \"Once deleted, you will not be able to recover this message!\",\n       icon: \"warning\",\n       buttons: {\n          cancel: true,\n          confirm: {\n            text: \"Proceed\",\n            value: true,\n            visible: true,\n            className: \"\",\n            closeModal: false\n          }\n        },\n       dangerMode: true,\n     })\n     .then((willDelete) => {\n       if (willDelete) {\n         return axios.post('api/delete-message', {id: message.id});\n\n       } else {\n         swal(\"Cancelled.\", {\n           icon: \"info\",\n         });\n         throw null;\n       }\n     })\n     .then(results => {\n       const status = results.data.status;\n\n       if (!status) {\n         return swal('Error', 'Something went wrong at the server. Message not deleted', 'error');\n       }\n       else {\n\n         let index = this.messages.indexOf(message);\n         this.messages.splice(index, 1);\n\n\n         swal({\n           title: \"Success\",\n           text: 'Messge deleted',\n           icon: 'success',\n         });\n       }\n\n\n     })\n     .catch(err => {\n       console.log(err);\n       if (err) {\n         swal(\"Oh noes!\", \"The AJAX request failed!\", \"error\");\n       } else {\n         swal.stopLoading();\n         swal.close();\n       }\n     });\n   },\n  }\n}\n</script>\n\n<style lang=\"css\">\n  td:nth-child(6){\n    white-space: nowrap;\n  }\n  td:nth-child(5){\n    white-space: normal !important;\n  }\n\n</style>\n"],"sourceRoot":""}]);

