exports.push([module.id, "\ntd:last-child{\n  white-space: nowrap;\n}\n", "", {"version":3,"sources":["/Applications/XAMPP/xamppfiles/htdocs/TanshiL5/app/Modules/PaymentAgent/Resources/js/components/SubComponents/app/Modules/PaymentAgent/Resources/js/components/SubComponents/SubViewUsers.vue"],"names":[],"mappings":";AAiKA;EACA,oBAAA;CACA","file":"SubViewUsers.vue","sourcesContent":["<template lang=\"html\">\n  <div class=\"container-fluid\">\n      <div class=\"row\">\n        <div class=\"col-md-12\">\n           <!--    Hover Rows  -->\n          <div class=\"panel panel-default\">\n            <div class=\"panel-heading\">\n                User Records\n            </div>\n            <div class=\"panel-body\">\n              <div class=\"table-responsive\">\n                <button type=\"button\" class=\"btn btn-success\" @click=\"$emit('switch-component', {data:null, comp:'CreateUser'})\">Create New User Account</button>\n                <input type=\"text\"class=\"pull-right col-md-4\" placeholder=\"Enter a search parameter\" v-model=\"searchText\">\n                <table class=\"table table-hover table-striped\">\n                  <thead>\n                    <tr class=\"default\">\n                      <th>Name</th>\n                      <th>Email</th>\n                      <th>Acc Balance</th>\n                      <th>Acc Status</th>\n                      <th>COT Code</th>\n                      <th>Transfer Permitted</th>\n                      <th>Actions</th>\n                    </tr>\n                  </thead>\n                  <tbody>\n                    <tr colspan=\"6\" class=\"loading text-center text-danger\" v-if=\"loading\">\n                        Loading...\n                    </tr>\n                    <tr v-if=\"error\" class=\"error\">\n                        <td colspan=\"6\">{{ error }}</td>\n                    </tr>\n                    <tr v-if=\"users\" v-for=\"user in users\" :key=\"user.id\">\n                      <td>{{ user.acc_name }}</td>\n                      <td>{{ user.email }}</td>\n                      <td>{{ user.acc_balance | currency }}</td>\n                      <td> {{ user.acc_status }}</td>\n                      <td> {{ user.cot_code }}</td>\n                      <td> {{ user.transder_permitted ? 'Yes' : 'No' }}</td>\n                      <td>\n                        <button class=\"btn btn-xs btn-info\" @click=\"$emit('switch-component', {data:user, comp:'ViewDetails'})\">Details</button>\n                        <!-- <button class=\"btn btn-xs btn-primary\" @click=\"$emit('switch-component', {data:user, comp:'NewTransaction'})\">Create Transaction</button> -->\n                        <button class=\"btn btn-xs btn-danger\" @click=\"deleteUser(user)\">Delete</button>\n                      </td>\n                    </tr>\n                  </tbody>\n                </table>\n              </div>\n            </div>\n          </div>\n        </div>\n      </div>\n    </div>\n\n</template>\n\n<script>\nexport default {\n  created() {\n    swal(\"Retrieving all users....\", {\n      buttons: false,\n    });\n    this.loading=true;\n      axios.get('api/get-all-users').then( rsp => {\n        this.users = rsp.data;\n        this.loading=false;\n        swal.stopLoading();\n        swal.close();\n      }, err => {\n        this.serverError(err);\n      });\n  },\n  data () {\n    return {\n      searchText: '',\n      error: '',\n      users: {},\n      loading: false\n    };\n  },\n  computed: {\n    filteredTransactions: function() {\n     var self = this;\n     if (this.searchText.length > 0) {\n       return this.transactions.filter(function(item) {\n         return self.searchText.indexOf(item.trans_type) > -1;\n       });\n     }\n     else {\n         return self.transactions;\n     }\n    },\n  },\n  methods: {\n   serverError(msg){\n     swal(\"Error!\", `${msg}`, \"error\");\n   },\n   deleteUser(user){\n     swal({\n       title: \"Are you sure?\",\n       text: \"Once deleted, you will not be able to recover this user account!\",\n       icon: \"warning\",\n       buttons: {\n          cancel: true,\n          confirm: {\n            text: \"Proceed\",\n            value: true,\n            visible: true,\n            className: \"\",\n            closeModal: false\n          }\n        },\n       dangerMode: true,\n     })\n     .then((willDelete) => {\n       if (willDelete) {\n         return axios.post('api/delete-user', {id: user.id});\n\n       } else {\n         swal(\"User account NOT deleted.\", {\n           icon: \"success\",\n         });\n         throw null;\n       }\n     })\n     .then(results => {\n       const status = results.data.status;\n\n       if (!status) {\n         return swal('Error', 'Something went wrong at the server. Account not deleted', 'error');\n       }\n       else {\n\n         let index = this.users.indexOf(user);\n         this.users.splice(index, 1);\n\n\n         swal({\n           title: \"Success\",\n           text: 'User deleted',\n           icon: 'success',\n         });\n       }\n\n\n     })\n     .catch(err => {\n       console.log(err);\n       if (err) {\n         swal(\"Oh noes!\", \"The AJAX request failed!\", \"error\");\n       } else {\n         swal.stopLoading();\n         swal.close();\n       }\n     });\n   },\n  }\n}\n</script>\n\n<style lang=\"css\">\n  td:last-child{\n    white-space: nowrap;\n  }\n</style>\n"],"sourceRoot":""}]);

